::ng-deep .mat-tooltip-panel {
  margin-top: 10px;
}

::ng-deep .mat-tooltip {
  background-color: #fff;
  color: #000 !important;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(209, 209, 209, 1);
  -moz-box-shadow: 1px 1px 1px 1px rgba(209, 209, 209, 1);
  box-shadow: 1px 1px 1px 1px rgba(209, 209, 209, 1);
  position: relative;
}

::ng-deep .mat-tooltip::after {
  width: 0;
  height: 0;
  content: '';
  position: absolute;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-bottom: 0.5rem solid #ffffff;
}

::ng-deep .mat-tooltip.below {
  overflow: initial;
  margin-top: 1rem;
}

::ng-deep .mat-tooltip.below:after {
  top: -0.5rem;
  right: calc(50% - 0.5rem);
  transform: rotate(0);
}


span.status-info-icon {
  position: absolute;
  bottom: 10PX;
  right: -16px;
}

::ng-deep .mat-radio-label-content {
  position: relative;
}

@media only screen and (min-device-width: 810px) and (max-device-width: 1180px) and (orientation: landscape) {
  .resp {
    display: none !important;
  }

  .respfilter {
